import { _util } from "./util.js";
import TheSlider from "@/assets/lib/TheSlider.cjs.1.0.102.js";

const images = [
  { path: require("@/assets/images/home1_1.jpg"), link: "#" },
  { path: require("@/assets/images/home1_2.jpg"), link: "#" },
  { path: require("@/assets/images/home1_3.jpg"), link: "#" },
  { path: require("@/assets/images/home1_4.jpg"), link: "#" },
  { path: require("@/assets/images/home1_5.jpg"), link: "#" },
  { path: require("@/assets/images/home1_6.jpg"), link: "#" },
  { path: require("@/assets/images/home1_7.jpg"), link: "#" }
];

const initSlider = async (target) => {
  try {
    await new TheSlider({
        target,
        images
    }).init(() => {
      _util.addRichMapClasses(target);
    });
  } catch (err) {
      console.error(err);
  }
}

const getMapMarkerSvgStr = (color1, color2) => {
    return `
    <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" width="36px" height="36px"
    viewBox="-4 0 36 36" version="1.1">
    <title>map-marker</title>
    <desc>Created with Sketch.</desc>
    <defs />
    <g id="Vivid.JS" stroke="none" stroke-width="1" fill="none" fill-rule="evenodd">
      <g id="Vivid-Icons" transform="translate(-125.000000, -643.000000)">
        <g id="Icons" transform="translate(37.000000, 169.000000)">
          <g id="map-marker" transform="translate(78.000000, 468.000000)">
            <g transform="translate(10.000000, 6.000000)">
              <path
                d="M14,0 C21.732,0 28,5.641 28,12.6 C28,23.963 14,36 14,36 C14,36 0,24.064 0,12.6 C0,5.641 6.268,0 14,0 Z"
                id="Shape" fill="${color1}" />
              <circle id="Oval" fill="${color2}" fill-rule="nonzero" cx="14" cy="14" r="7" />
            </g>
          </g>
        </g>
      </g>
    </g>
    </svg>
    `;
}

const getInfoWindowStr = (style) => {
  const strStyle1 = `
    <div class="slider"></div>
    <div class="details">
      <div class="title">Classic Style Home Bright Cozy East Williamsburg 2</div>
      <div class="description"><b>For $1M</b></div>
      <div class="rating">
        <div class="image"><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="#f73f58"><path d="M12 .587l3.668 7.568 8.332 1.151-6.064 5.828 1.48 8.279-7.416-3.967-7.417 3.967 1.481-8.279-6.064-5.828 8.332-1.151z"/></svg></div>
        <div class="desc">&nbsp;5.0</div>
      </div>
    </div>
  `;
  const strStyle2 = `
    <div class="slider"></div>
  `;
  switch (style) {
    case "style-1": return strStyle1;
    case "style-2": return strStyle2;
    case "style-3": return strStyle1;
    default: return strStyle1;
  }
}

const getInfoWindowDiv = (style) => {
  const div = document.createElement("div");
  div.setAttribute("class", `rich-info-window info-window-${style}`);
  div.innerHTML = getInfoWindowStr(style);
  return div;
}

const addSlider = async (target) => {
  await initSlider(target);
  _util.addRichMapClasses(target);
}

const getMapMarkerSvgStrStyle = (style) => {
  switch (style) {
    case "style-1": return getMapMarkerSvgStr("#FF6E6E", "#0C0058");
    case "style-2": return getMapMarkerSvgStr("red", "#0C0058");
    case "style-3": return getMapMarkerSvgStr("black", "white");
    case "style-4": return getMapMarkerSvgStr("#0175f7", "white");
    case "style-5": return getMapMarkerSvgStr("#f73f58", "white");
    default: return getMapMarkerSvgStr("#0175f7", "white");
  }
}

export const getMarker = (style) => {
  const div = document.createElement("div");
  div.setAttribute("class", `rich-marker marker-${style}`);
  div.innerHTML = getMapMarkerSvgStrStyle(style);
  return div;
}
export const getInfoWindow = (style) => {
  const infoWindow = getInfoWindowDiv(style);
  const slider = infoWindow.querySelectorAll(".slider")[0];
  //initSlider(slider);
  addSlider(slider);
  return infoWindow;
}