


class Util {

    MAX_Z_INDEX_MARKER = 2147483645;
    MAX_Z_INDEX_INFO_WINDOW = 2147483647;

    uuidv4() {
        return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
          var r = Math.random() * 16 | 0, v = c == 'x' ? r : (r & 0x3 | 0x8);
          return v.toString(16);
        });
    }

    addRichMapClasses = (domElement) => {
      domElement?.classList?.add("rich-map");
      for (var i = 0; i < domElement?.children?.length; i++) {
          var child = domElement.children[i];
          this.addRichMapClasses(child);
          child.classList.add("rich-map");
      }
  }

    POSITION = Object.freeze({
      
      TOP_LEFT: 1,
      TOP_CENTER: 2,
      TOP_RIGHT: 3,

      RIGHT_TOP: 4,
      RIGHT_CENTER: 5,
      RIGHT_BOTTOM: 6,

      BOTTOM_RIGHT: 7,
      BOTTOM_CENTER: 8,
      BOTTOM_LEFT: 9,

      LEFT_BOTTOM: 10,
      LEFT_CENTER: 11,
      LEFT_TOP: 12,

      CENTER: 13
      
    });

    wrapDomElement = (domElement) => {
      const div = document.createElement("div");
      div.classList.add("rich-map-wrapper");
      div.append(domElement);
      return div;
    }
}

//create an instance of the Util object (singleton).
const _util = new Util();

export { _util };