<template>
  <div id="map1" ref="map1" class="google-maps-1"></div>
</template>

<script>
//import RichMap from "../lib/rich-map.cjs.1.0.0.js";
import { getMarker, getInfoWindow } from "../modules/styles.js";

export default {
  name: "RichMap",
  props: {
    msg: String,
  },
  data: function () {
    return {
      data: "some data",
      richMap: null,
    };
  },
  // define methods under the `methods` object
  methods: {
    init() {
      window.onMapReady(() => {
        const RichMap = require("@/assets/lib/rich-map.cjs.1.0.0.js");
        this.richMap = RichMap;
        this.initMap();
        this.initRichMap();
      });
    },
    initMap() {
      //const myLatLng = { lat: -25.363, lng: 131.044 };
      const myLatLng = { lat: 8.016, lng: 133.153 };
      const map = new window.google.maps.Map(this.$refs.map1, {
          zoom: 3,
          center: myLatLng,
          disableDefaultUI: true,
      });
      window.map = map;
    },
    initRichMap() {
      // initialize the rich map
      const richMap = new this.richMap({
        map: window.map
      });

      // add some markers
      const richMarker = richMap.addMarker({
        domElement: getMarker("style-1"),
        LatLng: { lat: -25.363, lng: 131.044 },
        position: "bottomCenter"
      });
      const richMarker2 = richMap.addMarker({
        domElement: getMarker("style-2"),
        LatLng: { lat: -8.840, lng: 117.245 },
        position: "bottomCenter"
      });
      const richMarker3 = richMap.addMarker({
        domElement: getMarker("style-3"),
        LatLng: { lat: -4.852, lng: 145.288 },
        position: "bottomCenter"
      });
      const richMarker4 = richMap.addMarker({
        domElement: getMarker("style-4"),
        LatLng: { lat: 10.198, lng: 105.685 },
        position: "bottomCenter"
      });
      const richMarker5 = richMap.addMarker({
        domElement: getMarker("style-5"),
        LatLng: { lat: 33.626, lng: 119.748 },
        position: "bottomCenter"
      });

      // add info windows to markers
      const richInfoWindow = richMarker.addInfoWindow({
        domElement: getInfoWindow("style-1"),
        offset: "10px"
      });
      const richInfoWindow2 = richMarker2.addInfoWindow({
        domElement: getInfoWindow("style-2"),
        offset: "10px"
      });
      const richInfoWindow3 = richMarker3.addInfoWindow({
        domElement: getInfoWindow("style-3"),
        offset: "10px"
      });
      const richInfoWindow4 = richMarker4.addInfoWindow({
        domElement: getInfoWindow("style-1"),
        offset: "10px"
      });
      const richInfoWindow5 = richMarker5.addInfoWindow({
        domElement: getInfoWindow("style-1"),
        offset: "10px"
      });

      richInfoWindow;
      richInfoWindow2;
      richInfoWindow3;
      richInfoWindow4;
      richInfoWindow5;
    }
  },
  mounted: function () {
    this.init();
  },
};
</script>

